import { Formik } from "formik";
import React, { useEffect, useState } from "react";
import './Application.css';
import * as Scroll from 'react-scroll';


const Application = () => {
    const [messageSend, setmessageSend] = useState(false);
    useEffect(() => {
        //   first;

        return () => {
            // second;
        };
    }, [messageSend]);

    const sendForm = (e) => {
        const fetchResp = fetch('http://tvoykvest.ua', {
            method: 'POST',
            header: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify(e)
        })
            .then(response => response.text())
            .then(response => {
                setmessageSend(true);
            })
    }
    return (
        <div className="wrapper__application">
            <Scroll.Element name="scrollReserv"> <h1>  , -     </h1></Scroll.Element>
            {/* <h1>  , -     </h1> */}
            <Formik
                initialValues={{ text: '', tel: '', textarea: '' }}
                validate={values => {
                    const errors = {};
                    if (!values.text) {
                        errors.text = ' ';
                    }
                    if (!values.tel) {
                        errors.tel = ' ';
                    } else if (/[A-z-]/.test(values.tel)) {
                        errors.tel = '     ';
                    }
                    return errors;
                }}
                onSubmit={(values, actions) => {
                    sendForm(values);
                    actions.resetForm({
                        values: {
                            text: '',
                            tel: '',
                            textarea: ''
                        }
                    });
                }}
            >
                {({
                    values,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                    isSubmitting,
                }) => (
                    <form onSubmit={handleSubmit}>
                        <div>
                            {!touched.text && !errors.text && <div className="error__form">{errors.text}</div>}
                            <label htmlFor=""> :</label>
                            <input
                                type="text"
                                name="text"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.text}
                                placeholder=" "
                                maxLength={30}
                                required
                            />
                            {touched.text && errors.text && <div className="error__form">{errors.text}</div>}
                        </div>
                        <div>
                            <label htmlFor=""> :</label>
                            <input
                                type="tel"
                                name="tel"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.tel}
                                placeholder=""
                                minLength={11}
                                required
                            />
                            {touched.tel && errors.tel && <div className="error__form">{errors.tel}</div>}
                        </div>
                        <div>
                            <label htmlFor="">:</label>
                            <input
                                type="textarea"
                                name="textarea"
                                onChange={handleChange}
                                onBlur={handleBlur}
                                value={values.textarea}
                                placeholder=""
                            />
                        </div>
                        <button type="submit" disabled={isSubmitting}>
                            
                        </button>
                    </form>
                )}
            </Formik>
            {messageSend && <div>
                <p>          </p>
                <button>ok</button>
            </div>}
            
        </div>
    );
}

export default Application;