export const transformSelection = (index) => {
    // debugger;
    switch (index) {
        case 0:
            return ('/karantin');    
        case 1:
            return ('/autokvest');    
        case 2:
            return ('/childrenholidays');    
        case 3:
            return ('/corporate');    
        case 4:
            return ('/rent');    
        case 5:
            return ('/karantin');       
        default:
            break;
    }
}
