import React from "react";
import './Description.css';
import * as Scroll from 'react-scroll';
// import { Link, Button, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

const Description = () => {
    let Element = Scroll.Element;
    return (
        <div className="wrapper__aboutGame__description__double">
            <Element name="myScrollToElement">
                <div className="span__square" id="aboutGame">   150 .</div>
            </Element>
            <div className="wrapper__description__location">
                <div className="description__location">
                    
                    <span className="description__location__comment">   , ,     ...</span>
                </div>
                <div className="description__location">
                     
                    <span className="description__location__comment">     ?</span>
                </div>
                <div className="description__location">
                     
                    <span className="description__location__comment">   -  !</span>
                </div>
                <div className="description__location">
                     
                    <span className="description__location__comment"> : "    ",  , ,    </span>
                </div>
                <div className="description__location">
                     
                    <span className="description__location__comment">  -      </span>
                </div>
            </div>
        </div>
    );
}

export default Description;